export default {
	data() {
		return {
			posterUrl: '', //海报地址
		}
	},
	props: {
		_isShowBox: {
			type: Boolean,
			default: false
		},
		dear_id: Number
	},
	watch: {
		_isShowBox(val) {
			if(val && this.posterUrl == '') {
				this.toGetPosterCode()
			}
		}
	},
	methods: {
		toClosePosterBox() {
			this.$emit('toClosePosterBox', false)
		},
		toGetPosterCode() {
			uni.showLoading({
				title: '海报加载中',
				mask: true
			})
			this.$api.ajax({
				url: this.$config.getPosterCode,
				data: {
					dear_id: this.dear_id
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					this.posterUrl = res.data.data
					uni.hideLoading();
				}else {
					uni.hideLoading();
					this.$api.warnNotice(res.data.message)
					this.$emit('toClosePosterBox', false)
				}
			})
			.catch(() => {
				uni.hideLoading();
				this.$api.warnNotice('获取海报失败')
				this.$emit('toClosePosterBox', false)
			})
		},
		saveImg() {
			const $this = this
			uni.showModal({
				title: '',
				content: '由于当前小程序无法分享到朋友圈，是否将下载海报到本地相册',
				cancelColor: '#909399',
				confirmColor: '#FF0014',
				success: (res) => {
					if(res.confirm) {
						$this.saveImgMay()
					}
				}
			})
		},
		saveImgMay() {
			const $this = this
			uni.downloadFile({
			  url: $this.posterUrl,
			  success: function (res) {
			    uni.getSetting({
			      success(r) {
			        if (!r.authSetting['scope.writePhotosAlbum']) {
			          uni.authorize({
			            scope: 'scope.writePhotosAlbum',
			            success() {
			              uni.saveImageToPhotosAlbum({
			                filePath: res.tempFilePath,
			                success(result) {
								uni.showToast({
			                      title: '保存成功',
			                      icon: 'success',
								  success: () => {
									setTimeout(() => {
										$this.$emit('toClosePosterBox', false)
									}, 1500)
								  }
			                    })
			                }
			              })
			            },
						fail(err) {
							$this.$api.warnNotice('您已拒绝授权，如需重新获得该功能请删除小程序后重新下载')
						}
			          })
			        }else {
						uni.saveImageToPhotosAlbum({
						  filePath: res.tempFilePath,
						  success(result) {
							uni.showToast({
						        title: '保存成功',
						        icon: 'success',
								success: () => {
									setTimeout(() => {
										$this.$emit('toClosePosterBox', false)
									}, 1500)
								}
						    })
						  }
						})
					}
			      },
				  fail(err) {
					  console.log(err)
					 $this.$api.warnNotice('操作失败')
				  }
			    })
			  },
			  fail(err) {
				  console.log(err)
				  $this.$api.warnNotice('操作失败')
			  }
			})
		}
	}
}
